export default {
  data() {
    return {
			user_id: ''
		};
	},
	onLoad() {
		this.user_id = uni.getStorageSync("user_id");
		// 提现账户接口
		this.getAcc()
	},
  methods: {
		// 提现账户接口
		getAcc() {
			this.$http.get('/wallet/getAcc', { params: {user_id:this.user_id} }).then(res => {
				console.log(res)
			})
		},
		// 去绑定银行卡页面
		goWalletBindingCard() {
			uni.navigateTo({
        url: '/pages/walletBindingCard/walletBindingCard'
      })
		}
	}
};
